import React from "react";
import FRNLineRow from "./FRNLineRow";

function FRNDetails(props) {
  if (props.data.length !== 0) {
    return (
      <div>
        <table>
          <tr>
            <th>FRN Line</th>
            <th width={"512px"}>Model</th>
            <th>Cost</th>
            <th>Ineligible</th>
            <th>Eligible</th>
            <th>Qty</th>
            <th>Tot Eligible</th>
          </tr>
          {props.data.map((createRow) => (
            <FRNLineRow line={createRow} />
          ))}
        </table>
      </div>
    );
  } else {
    return <div />;
  }
}

export default FRNDetails;
