import React from "react";
import FRNHeadingRow from "./FRNHeadingRow";

function FRNHeading(props) {
  var formatter = new Intl.NumberFormat("en-US", {
    style: "currency",
    currency: "USD"
  });

  function formatDate(date) {
    var d = new Date(date),
      month = "" + (d.getMonth() + 1),
      day = "" + d.getDate(),
      year = d.getFullYear();

    if (month.length < 2) month = "0" + month;
    if (day.length < 2) day = "0" + day;

    return [month, day, year].join("-");
  }

  return (
    <div>
      <h3>
        {props.headObj.funding_request_number} - {props.headObj.nickname}
      </h3>
      <table>
        <FRNHeadingRow
          label={"Service Provider:"}
          data={props.headObj.spin_name}
        />
        <FRNHeadingRow
          label={"Application Number:"}
          data={props.headObj.application_number}
        />
        <FRNHeadingRow
          label={"Applicant Name:"}
          data={props.headObj.organization_name}
        />
        <FRNHeadingRow
          label={"FRN Status:"}
          data={props.headObj.form_471_frn_status_name}
        />
        <FRNHeadingRow
          label={"Contract:"}
          data={props.headObj.contract_number}
        />
        <FRNHeadingRow
          label={"Svc Delivery Deadline:"}
          data={formatDate(props.headObj.service_delivery_deadline)}
        />
        <FRNHeadingRow
          label={"Contract Expiration:"}
          data={formatDate(props.headObj.contract_expiration_date)}
        />
        <FRNHeadingRow
          label={"Expiration Date - all Extensions:"}
          data={formatDate(props.headObj.extended_expiration_date)}
        />
        <FRNHeadingRow
          label={"Eligible Amt:"}
          data={formatter.format(props.headObj.total_pre_discount_costs)}
        />
        <FRNHeadingRow
          label={"Discount %:"}
          data={props.headObj.dis_pct * 100}
        />
        <FRNHeadingRow
          label={"Funded Amt:"}
          data={formatter.format(props.headObj.funding_commitment_request)}
        />
        <FRNHeadingRow
          label={"Last Day to Invoice:"}
          data={formatDate(props.headObj.last_date_to_invoice)}
        />
        <FRNHeadingRow
          label={"Disbursement Total:"}
          data={formatter.format(props.headObj.total_authorized_disbursement)}
        />
      </table>
      <br />
    </div>
  );
}

export default FRNHeading;
