import React from "react";
import FRNHeading from "./FRNHeading";
import FRNDetails from "./FRNDetails";

const https = require("https");

function App() {
  const [frn, setFRN] = React.useState("");
  const [frnHeader, setfrnHeader] = React.useState([]);
  const [frnDetail, setfrnDetail] = React.useState([]);

  function getFRNData(frn) {
    // ******** FRN HEADER DETAIL ********
    const url1 =
      "https://opendata.usac.org/resource/qdmp-ygft.json?form_version=Current&funding_request_number=" +
      frn;
    https.get(url1, function (response) {
      if (response.statusCode === 200) {
        response.on("data", function (data) {
          setfrnHeader(JSON.parse(data));
        });
        // } else {
        //   console.log("API Error: " + response.statusCode);
        //   return null;
      }
    });
  }

  function getFRNDetail(frn) {
    // ****** FRN LINE ITEM DETAIL **********
    const url2 =
      "https://opendata.usac.org/resource/hbj5-2bpj.json?form_version=Current&funding_request_number=" +
      frn;
    https.get(url2, function (response) {
      if (response.statusCode === 200) {
        var frnLineData = [];
        response.on("data", function (data) {
          frnLineData.push(data);
        });
        response.on("end", function () {
          const fData = Buffer.concat(frnLineData);
          setfrnDetail(JSON.parse(fData));
        });
        // } else {
        //   console.log("API Error: " + response.statusCode);
        //   return null;
      }
    });
  }

  function handleClick() {
    // setfrnHeader((prevItems) => {
    //   return [getFRNData(frn)];
    // });
    getFRNData(frn);
    getFRNDetail(frn);
    setFRN("");
  }

  function handleChange(event) {
    setFRN(event.target.value);
  }

  function createHeader(data) {
    return <FRNHeading headObj={frnHeader[0]} />;
  }

  function createDetail() {
    return <FRNDetails data={frnDetail} />;
  }

  // getFRNData("2199060314");
  return (
    <div>
      <input onChange={handleChange} type="text" name="frn" value={frn} />
      <button onClick={handleClick}>
        <span>Submit FRN</span>
      </button>
      <hr />
      {frnHeader.map(createHeader)}
      {createDetail()}
    </div>
  );
}

export default App;
